              2. 회원은 개인정보에 변동이 있을 경우, 즉시 '내 정보 수정' 메뉴 및 '피드백' 문의 창구를 이용하여 정보를 최신화해야 합니다.<br>
              3. 회원의 아이디, 비밀번호, 본인확인정보(이름, 학번, 학과, 전화번호 등) 등 모든 개인정보의 관리책임은 본인에게 있으므로, 타인에게 양도 및 대여할 수 없으며 유출되지 않도록 관리해야 합니다. 만약 본인의 아이디 및 비밀번호를 타인이 사용하고 있음을 인지했을 경우, 즉시 문의 창구로 알려야 하고, 안내가 있는 경우 이에 따라야 합니다.<br>
              4. 동구라미는 회원이 제2항과 3항을 이행하지 않아 발생한 피해에 대해, 동구라미의 고의 또는 중대한 과실이 없는 한 어떠한 책임도 지지 않습니다.
            </p>
