          <section class="terms-section">
            <h3>제 7조 (서비스 이용계약의 종료)</h3>
            <p class="indent">
              1. 회원은 언제든지 본인의 계정으로 로그인한 뒤 서비스 내부의 '회원탈퇴' 버튼을 누르는 방식으로 탈퇴를 요청할 수 있으며, 그 외 문의 창구 등을 통한 탈퇴 요청은 처리되지 않습니다. 동구라미는 해당 요청을 확인한 후 탈퇴를 처리합니다.<br>
              2. 동구라미는 회원이 제10조(금지행위)에 해당하는 행위를 하거나 해당하는 행위를 했던 이력이 있을 경우, 제11조(서비스 제공의 중단 및 서비스 이용계약의 해지)에 따라 서비스 제공을 중단하거나 서비스 이용계약을 해지할 수 있습니다.<br>
              3. 동구라미는 제1항부터 제2항까지로 인해 발생한 피해에 대해 동구라미의 고의 또는 중대한 과실이 없는 한 어떠한 책임도 지지 않습니다.
            </p>
          </section>

          <section class="terms-section">
            <h3>제 8조 (회원에 대한 통지)</h3>
            <p class="indent">
              1. 동구라미가 회원에 대한 통지가 필요한 경우, 회원의 연락처 또는 서비스 내부 알림 수단을 이용할 수 있습니다.<br>
              2. 동구라미는 회원 전체에 대한 통지의 경우 공지사항에 게시함으로써 전 항의 통지에 갈음할 수 있습니다.<br>
              3. 동구라미가 회원에게 "30일 이내에 의사를 표시하지 아니할 경우 동의한 것으로 간주한다"는 내용을 고지하였음에도 불구하고 회원이 의사를 표시하지 아니하면, 동구라미는 통지 내용에 동의한 것으로 간주합니다.
            </p>
          </section>

          <section class="terms-section">
            <h3>제 9조 (광고의 게재 및 발신)</h3>
            <p class="indent">
              1. 동구라미는 서비스의 제공을 위해 서비스 내부에 광고를 게재할 수 있습니다.<br>
              2. 동구라미는 이용자의 이용 기록을 활용한 광고를 게재할 수 있습니다.<br>
              3. 동구라미는 회원이 광고성 정보 수신에 명시적으로 동의한 경우, 회원이 동의한 수단을 통해 광고성 정보를 발신할 수 있습니다.<br>
              4. 동구라미는 광고 게재 및 동의된 광고성 정보의 발신으로 인해 발생한 피해에 대해 동구라미의 고의 또는 중대한 과실이 없는 한 어떠한 책임도 지지 않습니다.
            </p>
          </section>

          <section class="terms-section">
            <h3>제 10조 (금지행위)</h3>
            <p class="indent">
              1. 이용자는 다음과 같은 행위를 해서는 안됩니다.<br>
              A. 성적 도의관념에 반하는 행위<br>
              <p class="double-indent">
                i. 정보통신망 이용촉진 및 정보보호 등에 관한 법률에 따른 유해정보 유통 행위<br>
                ii. 전기통신사업법에 따른 불법 촬영물 등 유통 행위<br>
                iii. 청소년보호법에 따른 청소년유해매체물 유통 행위<br>
                iv. 방송통신심의위원회의 정보통신에 관한 심의규정에 따른 심의기준의 성적 도의관념에 반하는 행위<br>
